import {
    useMutation,
    UseMutationOptions,
    UseMutationResult
} from '@tanstack/react-query';
import {SocialAuthLoginCommand, SocialAuthResult} from '../../types';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import baseUrl from '../baseUrl';
import {handleResponse} from '../handleResponse';

const useSocialAuthLogin = (
    options?: UseMutationOptions<
        SocialAuthResult,
        Error,
        SocialAuthLoginCommand
    >
): UseMutationResult<SocialAuthResult, Error, SocialAuthLoginCommand> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();

    return useMutation({
        mutationFn: async (command: SocialAuthLoginCommand) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/auth/social_login/login`,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify(command)
                }
            );
            return handleResponse(response);
        },
        ...options
    });
};

export default useSocialAuthLogin;
